html, body {
    max-width: 100%;
    overflow-x: hidden;
}

button:focus { 
	outline:0;
}

ul {
	padding-left: 0;
  	list-style-type: none;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", 'DM Sans',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
	text-decoration: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* People section */
.ellipse-up {
  background: url("./images/ellipse_up.png");
  background-size: 100%;
  background-repeat: no-repeat;
  height: 300px;
  margin-top: 100px;
}

/* Header section */
.ellipse-down {
  background: url("./images/ellipse_up.png");
  background-size: 100%;
  background-repeat: no-repeat;
  height: 300px;
  transform: rotate(180deg);
  margin-bottom: 5rem;
}

/* Start of the profile photos */
.contributors {
	 display: flex;
	 flex-wrap: wrap;
	 padding: 3rem 6rem 0 6rem;
	 justify-content: center;
}
 .contributors .contributor {
	 list-style-type: none;
	 width: 70px;
	 height: 70px;
	 position: relative;
	 margin: 1.2rem;
}
 .contributors .contributor:hover .contributor-info {
	 visibility: visible;
	 opacity: 1;
}
 .contributors .contributor img {
	 width: 70px;
	 height: 70px;
	 border-radius: 50%;
}
 .contributors .contributor .contributor-info {
	 width: 190px;
	 height: 240px;
	 background: white;
	 box-shadow: 0px 4px 8px rgba(0, 0, 0, .2);
	 border-radius: 2px;
	 position: absolute;
	 top: -30px;
	 left: -45px;
	 display: flex;
	 flex-direction: column;
	 align-items: center;
	 justify-content: center;
	 transition: all 0.3s ease;
	 opacity: 0;
	 visibility: hidden;
	 z-index: 9;
}
 .contributors .contributor .contributor-info b {
	 font-weight: manrope-bold;
	 margin: 15px 0 8px;
	 line-height: 19px;
	 font-size: 14px;
}
 .contributors .contributor .contributor-info span {
	 font-family: ibm-plex-mono;
	 line-height: 15px;
	 font-size: 12px;
	 color: #9299a3;
	 text-align: center;
	 padding: 0 10px;
	 height: 15px;
	 max-width: 100%;
}
 .contributors .contributor .contributor-info ul {
	 list-style-type: none;
	 display: flex;
	 margin: 25px 0 0 0;
	 padding: 0;
}
 .contributors .contributor .contributor-info ul li a {
	 margin: 0 10px;
}
 .contributors .contributor .contributor-info ul li a img {
	 width: 26px;
	 height: 26px;
}

/* End of the profile photos */

.center {
    display: flex;
	justify-content: center;
}

.mobile-nav {
	display: none;
}

.mobile-nav-menu {
	background:white;
	box-shadow: 0px 2px 14px rgba(9, 9, 9, 0.25);
	border-radius: 4px;
	position: fixed;
	padding: 1rem;
	flex-direction: column;
	width: 90%;
	z-index: 2;
	left:0;
	right:0;
	margin-left:auto;
	margin-right:auto;
	top: 1rem;
}

.mobile-nav-menu ul{
	list-style: none;
	position: relative;
}

.padding-top-10 {
	padding-top: 10px;
}

.event-bg {
	background-image: url("./images/event.png");
	background-color: black;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	height: 100%
}

.content-bg {
	background-image: url("./images/content.png");
	background-color: black;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	height: 100%
}

.community-bg {
	background-image: url("./images/community.png");
	background-color: black;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	height: 100%
}

.technical-bg {
	background-image: url("./images/technical.png");
	background-color: black;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	height: 100%
}

.cta-top{
	position: relative;
	top: 10rem;
}

.carousel-center {
	display: flex;
	justify-content: center;
}

.table-link {
	color: white !important;
	text-decoration: underline;
}

.table-link:visited {
	color: white !important;
}

.table-link:hover {
	color: #9299a3 !important;
}


@media screen and (min-width: 1025px) {
  .carousel-display{
	display: none;
  }
}


@media screen and (max-width: 1024px) {
  .box-display{
    display: none !important;
  }
  .carousel-center ul{
	  padding-left: 0;
	  padding-top: 2rem;
  }

  ul{
	  padding-left: 0;
  }
}

@media screen and (max-width: 900px) {
	.mobile-nav {
		display: flex;
		align-items: center;
		width: calc(100% - 20px);
	}

	.mobile-logo {
		margin: 1.1rem 30vw 1rem 2rem;
	}

	.mobile-menu {
		position: absolute;
		right: 1.6rem;
		border: none;
		background: none;
	}

	.mobile-menu:focus {
		outline:0;
	}

}

.tg  {
	border-collapse: collapse;
	border-spacing: 0;
	margin: auto;
	width: 800px;
}

.tg td {
	border-color: black;
	border-style: solid;
	border-width: 1px;
	font-family: Arial, sans-serif;
	font-size: 14px;
	overflow: hidden;
	padding: 9px 20px;
	word-break: normal;
}

.tg th { 
	border-color: black;
	border-style: solid;
	border-width: 1px;
	font-family: Arial, sans-serif;
	font-size: 14px;
	font-weight: normal;
	overflow: hidden;
	padding: 9px 20px;
	word-break: normal;
}

.tg .tg-header {
	background-color: #242424;
	border-color: #ffffff;
	color: #ffffff;
	text-align: center;
	vertical-align: middle
}

.tg .tg-center {
	background-color: #000000;
	border-color: #ffffff;
	color: #ffffff;
	text-align: center;
	vertical-align: middle
}

.tg .tg-left {
	background-color: #000000;
	border-color: #ffffff;
	color: #ffffff;
	text-align: left;
	vertical-align: middle
}

.align-center {
	display: block;
	margin: 10px auto;
	width: 400px;
}

.reward-font {
	font-size: 1.1rem !important;
}

.header-font {
	font-size: 1.2rem !important;
	font-weight: bold !important;
}

.control-arrow {
	padding: 0 30px !important;
}

.mobile {
	display: none;
}

.description-header {
	font-size: 1.2rem; 
	font-weight: bold; 
	line-height: 2;
}

.description-content {
	font-size: 1rem; 
	line-height: 1.5;
}

@media screen and (min-width: 1140px) {
	.control-next.control-arrow:before {
		border-left: 20px solid #fff !important;
		border-top: 20px solid transparent !important;
		border-bottom: 20px solid transparent !important;
	}
	
	.control-prev.control-arrow:before {
		border-right: 20px solid #fff !important;
		border-top: 20px solid transparent !important;
		border-bottom: 20px solid transparent !important;
	}
}


@media screen and (max-width: 1140px) {
	.desktop {
		display: none;
	}

	.mobile {
		display: block;
	}

	.align-center {
		width: 90%;
	}

	.description-header {
		font-size: 1.05rem;
		line-height: 1.5;
	}
	
	.description-content {
		font-size: 0.9rem; 
		line-height: 1.2;
	}

	.tg {
		width: 100%;
	}

	.header-font {
		font-size: 1.1rem !important;
	}

	.reward-font {
		font-size: 1rem !important;
	}
}
  


@media screen and (max-width: 767px) {

	.contributors {
		padding: 3rem 3rem 0 3rem;
   }

	.ellipse-down {
		height: 200px;
		margin-bottom: 3rem;
	}

	.align-center {
		width: 98%;
	}


	.mobile-nav-menu {
		padding: 0.5rem;
	}

	.center {
		margin-top: 2rem;
	}

    @media(max-width: 767px) {

		.cta-top{
        	top: 6rem;
		}
    }

}

@media screen and (max-width: 600px) {

	.align-center {
		width: 100%;
	}

	.tg th {
		padding: 9px 0px !important;
	}

	.header-font {
		font-size: 1rem !important;
	}

}